<template>
  <div class="px-3 py-2">
    <div class="row">
      <div class="col-xxl-6 col-12">
        <div class="row">
          <div class="col-auto">
            <button @click="getHTTP()">OK</button>
            <img
              class="image-profile-detail"
              :src="
                Boolean(activeVehicle.driverId)
                  ? getImgUrl(`driver/${activeVehicle.driverId}.png`)
                  : getImgUrl(`driver/profile.png`)
              "
            />
          </div>
          <div class="col">
            <!-- Row 1 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">ชื่อรถ</span>
                <span class="text-detail-content">{{
                  vehicleDetail.vehicleName == null
                    ? "-"
                    : vehicleDetail.vehicleName
                }}</span>
              </div>
              <div class="col">
                <span class="text-detail-header">กลุ่ม</span>
                <span class="text-detail-content">
                  {{
                    vehicleDetail.groupName == null
                      ? "-"
                      : vehicleDetail.groupName
                  }}</span
                >
              </div>
            </div>
            <!-- Row 2 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">ทะเบียน</span>
                <span class="text-detail-content">{{
                  vehicleDetail.licenseCode == null
                    ? "-"
                    : vehicleDetail.licenseCode
                }}</span>
              </div>
              <div class="col">
                <span class="text-detail-header">สถานะ</span>
                <span class="text-detail-content">
                  {{
                    vehicleDetail.statusName == null
                      ? "-"
                      : vehicleDetail.statusName
                  }}</span
                >
              </div>
            </div>
            <!-- Row 3 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">ความเร็ว</span>
                <span class="text-detail-content"
                  >{{ vehicleDetail.speed == null ? "-" : vehicleDetail.speed }}
                  Km./h.
                </span>
              </div>
              <div class="col">
                <span class="text-detail-header">อุณหภูมิ</span>
                <span class="text-detail-content"
                  >{{ vehicleDetail.temp == null ? "-" : vehicleDetail.temp }}°
                </span>
              </div>
            </div>
            <!-- Row 4 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">อัพเดท</span>
                <span class="text-detail-content"
                  >{{
                    vehicleDetail.update == null ? "-" : vehicleDetail.update
                  }}
                </span>
              </div>
              <div class="col">
                <span class="text-detail-header">พิกัด</span>

                <span class="text-detail-content"
                  >{{
                    vehicleDetail.position == null
                      ? "-"
                      : vehicleDetail.position.lat | slice(4, "number")
                  }},
                  {{
                    vehicleDetail.position == null
                      ? "-"
                      : vehicleDetail.position.lng | slice(4, "number")
                  }}
                </span>
              </div>
            </div>
            <!-- Row 5 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">ผู้ขับขี่</span>
                <span class="text-detail-content"
                  >{{
                    vehicleDetail.driverName == null
                      ? "-"
                      : vehicleDetail.driverName
                  }}
                  ({{
                    vehicleDetail.driverId == null
                      ? "-"
                      : vehicleDetail.driverId
                  }})</span
                >
              </div>
            </div>
            <!-- Row 6 -->
            <div class="row pb-1 g-0">
              <div class="col">
                <span class="text-detail-header">ตำแหน่ง</span>
                <span class="text-detail-content"
                  >{{
                    vehicleDetail.location == null
                      ? "-"
                      : vehicleDetail.location
                  }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-xxl-6 col-12">
        <line-chart :width="1000" :height="220"></line-chart>
      </div>
    </div>

    <!-- {{ activeVehicle }} -->
  </div>
</template>

<script>
import LineChart from "./LineChart.vue";
import axios from "axios";
export default {
  components: {
    LineChart,
  },
  created() {},
  data() {
    return {
      activeVehicle: Object,
    };
  },
  computed: {
    vehicleDetail() {
      if (this.vehicleProp != null) {
        this.activeVehicle = this.vehicleProp;
      }
      return this.activeVehicle;
    },
  },
  methods: {
    getImgUrl(pic) {
      return require("../../assets/" + pic);
    },
    async getHTTP() {
      const response = await axios.get("http://192.168.1.4:5400/say1");
      console.log("response => " , response);
    },
  },
  props: {
    vehicleProp: Object,
  },
};
</script>

<style scoped>
.image-profile-detail {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.text-detail-header {
  color: rgb(0, 119, 230);
  margin-right: 5px;
}
</style>